import React from 'react';
import css from './contact.module.scss';
import {TextField} from "@material-ui/core";
import Button from "@material-ui/core/Button";
import FooterWhiteMask from "../../assets/images/footer-white-mask.png";
import github from "../../assets/images/github--v1.png";
// import github from "../../assets/images/github--v1.png";

export default class Contact extends React.Component {
    constructor(props: any) {
        super(props);
        this.submitForm = this.submitForm.bind(this);
        this.state = {
            status: ""
        };
    }

    render() {
        // @ts-ignore
        const {status} = this.state;
        return (
            <div className={css.container} id='contact'>
                <div className={css.container}>
                    <img src={FooterWhiteMask} alt=''/>
                </div>
                <div className={css.form}>
                    <form
                        onSubmit={this.submitForm}
                        action="https://formspree.io/f/mwkwwebp"
                        method="POST"
                    >
                        <h3>Let's Talk!</h3>
                        <p className={css.subtitle}>I will get back to you within one to two days through email. Also
                            please don't forget to check your spam account just in case!</p>
                        <TextField label="Name *" />
                        <TextField label="Email *" />
                        <TextField label="Subject *" />
                        <TextField style={{marginBottom: 40}}
                                   label="Message *" multiline rows={4}/>
                        {status === "SUCCESS" ?
                            <p>Thanks!</p> :
                            <Button variant="outlined" color="secondary" type='submit'>
                                Submit
                            </Button>
                        }
                        {status === "ERROR" && <p>Ooops! There was an error.</p>}
                    </form>
                    <div className={css.contact}>
                        <div>
                            {/*<img src={} alt=""/>*/}
                            <img src="https://img.icons8.com/clouds/50/000000/apple-mail.png"/>
                            <h4>med.yessine.b.k@gmail.com</h4>
                        </div>
                        <div>
                            <img src={github} alt=""/>
                            <h4>https://github.com/adhess</h4>
                        </div>
                    </div>
                </div>

            </div>
        );
    }

    submitForm(ev: any) {
        ev.preventDefault();
        const form = ev.target;
        const data = new FormData(form);
        const xhr = new XMLHttpRequest();
        xhr.open(form.method, form.action);
        xhr.setRequestHeader("Accept", "application/json");
        xhr.onreadystatechange = () => {
            if (xhr.readyState !== XMLHttpRequest.DONE) return;
            if (xhr.status === 200) {
                form.reset();
                this.setState({status: "SUCCESS"});
            } else {
                this.setState({status: "ERROR"});
            }
        };
        xhr.send(data);
    }
}
